	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"He doesn't even like to leave Bavaria","date":"16 Jul 2005","slug":"/2005-07-16-he-doesnt-even-like-to-leave-bavaria/","tags":"Y2005"},"html":"<p>Today's Economist print-edition article on <a href=\"http://www.economist.com/World/europe/displayStory.cfm?story_id=4174513\" title=\"German foreign policy | Rebalancing, not realigning | Economist.com\">German foreign policy</a> (subscribers only): \"What many in Berlin dread most is that Edmund Stoiber, boss of the CDU's Bavarian sister party, the Christian Social Union (CSU), insists on being foreign minister. That would give him a top post, but let him stand above the political fray: it is easier to be popular as foreign minister than as, say, finance minister. Yet Mr Stoiber would hate the travelling, says one insider: 'He doesn't even like to leave Bavaria.'\"</p>"},"next":{"fields":{"title":"90% of Everything is Crap","date":"16 Jul 2005","slug":"/2005-07-16-90-of-everything-is-crap/"}},"previous":{"fields":{"title":"When In Doubt, Throw It Out","date":"17 Jul 2005","slug":"/2005-07-17-when-in-doubt-throw-it-out/"}}},{"node":{"fields":{"title":"90% of Everything is Crap","date":"16 Jul 2005","slug":"/2005-07-16-90-of-everything-is-crap/","tags":"Y2005"},"html":"<p>It <a href=\"http://www.olbertz.de/archives/000553.html\" title=\"The blog that never sleeps - Lustige Kolumne\">doesn't</a> <a href=\"http://lumma.de/eintrag.php?id=1879\" title=\"Kolumnenm&uuml;llproduzent [Lummaland - das Weblog]\">take</a> <a href=\"http://www.hebig.com/archives/003235.shtml\" title=\"Beendet die wilde Web-Verschmutzung! by Heiko Hebig | hebig.com\">much</a> to get the German blogosphere riled up. This time it was a <a href=\"http://www.netzwelt.de/news/71901_1-kolumne-stoppt-die-blogexhibitionisten.html\" title=\"Kolumne: Stoppt die Blog-Exhibitionisten!\">column</a> at <a href=\"http://www.netzwelt.de/\" title=\"netzwelt.de - Computernews, Testberichte, Software und MP3 Downloads\">netzwelt.de</a> that asserted that most weblogs are crap, although there are a few good ones. So what? That's true of anything, even online columns. Of course, crap is in the eyes of the beholder, and one person's crap is another person's treasure. The column mentioned <a href=\"http://umstandsmoden.blogg.de/\" title=\"Umstandsmode, Schwangerschaft, Geburt und Stillen, Baby und Kind ...\">maternity wear</a> as a topic that interests noone. But if you happen to be in the fashion industry or are perhaps pregnant, the tips could be valuable.</p>\n<p>So was the netzwelt article itself crap? I thought it might have been funny, except Germans don't do <a href=\"http://lumma.de/eintrag.php?id=1873\" title=\"UEK-Fanclub [Lummaland - das Weblog]\">irony</a>.</p>"},"next":{"fields":{"title":"Wanna Ruin Your Resumé?","date":"15 Jul 2005","slug":"/2005-07-15-wanna-ruin-your-resum/"}},"previous":{"fields":{"title":"He doesn't even like to leave Bavaria","date":"16 Jul 2005","slug":"/2005-07-16-he-doesnt-even-like-to-leave-bavaria/"}}},{"node":{"fields":{"title":"Wanna Ruin Your Resumé?","date":"15 Jul 2005","slug":"/2005-07-15-wanna-ruin-your-resum/","tags":"Y2005"},"html":"<p>If you're female, <a href=\"http://www.washingtonmonthly.com/archives/individual/2005_07/006730.php\" title=\"The Washington Monthly\">giving birth to a child will ruin your resum&eacute;</a> for upper-management jobs.</p>"},"next":{"fields":{"title":"Not That Starbuck","date":"15 Jul 2005","slug":"/2005-07-15-not-that-starbuck/"}},"previous":{"fields":{"title":"90% of Everything is Crap","date":"16 Jul 2005","slug":"/2005-07-16-90-of-everything-is-crap/"}}}],"pathPrefix":"","first":false,"last":false,"index":294,"pageCount":1101}}

